// Dashboard.tsx
import {
  CalendarMonth,
  CurrencyRupeeRounded,
  StarRateRounded,
  TrendingDown,
  TrendingUp,
} from "@mui/icons-material";
import {
  Box,
  Card,
  CardContent,
  FormControl,
  Grid,
  IconButton,
  MenuItem,
  Select,
  Typography,
  Button,
  Avatar,
  Divider,
} from "@mui/material";
import {
  CartesianGrid,
  Line,
  LineChart,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from "recharts";
import color from "../../components/color";
import BookingTable from "./BookingTable";
import { useEffect, useState } from "react";
import {
  getAllHotels,
  getAllMyBookings,
  getAllRatings,
  getAllHotelRevenue,
} from "../../services/services";
import { getUserId } from "../../services/axiosClient";
import { useNavigate } from "react-router-dom";
import dayjs from "dayjs";

const CARD_HEIGHT = 140;

const Dashboard: React.FC = () => {
  const navigate = useNavigate();

  // chart placeholder
  const [chartData, setChartData] = useState<any[]>([
    { name: "Dec 27", revenue: 100000 },
    { name: "Jan 28", revenue: 250000 },
    { name: "Feb 28", revenue: 215060 },
  ]);

  // state
  const [hotels, setHotels] = useState<Array<{ id: string; name: string }>>([]);
  const [hotelBookings, setHotelBookings] = useState<Record<string, any[]>>({});
  const [selectedHotelId, setSelectedHotelId] = useState<string>("");
  const [newBookingsCount, setNewBookingsCount] = useState<number>(0);

  // ratings
  const [ratingHotelId, setRatingHotelId] = useState<string>("");
  const [ratingsMap, setRatingsMap] = useState<Record<string, { avg: number; count: number }>>(
    {}
  );
  const [ratingsListMap, setRatingsListMap] = useState<Record<string, any[]>>({});

  // revenue
  const [revenueMap, setRevenueMap] = useState<Record<string, { total: number }>>({});

  // fetch hotels + bookings + metrics
  useEffect(() => {
    const fetchHotels = async () => {
      const payLoad = {
        data: { filter: "", userId: getUserId() },
        page: 0,
        pageSize: 50,
        order: [["createdAt", "ASC"]],
      };

      try {
        const res = await getAllHotels(payLoad);
        if (res?.data?.data?.rows) {
          const hotelData = res.data.data.rows.map((hotel: any) => ({
            id: hotel.id,
            name: hotel.propertyName ?? hotel.name ?? `Hotel ${hotel.id}`,
          }));
          setHotels(hotelData);
          if (hotelData.length > 0) {
            setSelectedHotelId((prev) => prev || hotelData[0].id);
            setRatingHotelId((prev) => prev || hotelData[0].id);
          }
          return hotelData.map((hotel: any) => hotel.id);
        }
      } catch (error) {
        console.error("Error fetching hotels:", error);
      }
      return [];
    };

    const fetchBookingsForHotels = async (ids: string[]) => {
      if (ids.length === 0) return;
      const bookingsMap: Record<string, any[]> = {};
      await Promise.all(
        ids.map(async (hotelId) => {
          try {
            const payLoad = {
              data: { filter: "", hotelId },
              page: 0,
              pageSize: 50,
              order: [["checkInDate", "ASC"]],
            };
            const bookingsRes = await getAllMyBookings(payLoad);
            bookingsMap[hotelId] = bookingsRes?.data?.data?.rows || [];
          } catch (error) {
            console.error(`Error fetching bookings for hotel ${hotelId}:`, error);
            bookingsMap[hotelId] = [];
          }
        })
      );
      setHotelBookings(bookingsMap);
    };

    fetchHotels().then(async (ids) => {
      await fetchBookingsForHotels(ids ?? []);
      await fetchNewBookingsCount();
      if (ids && ids.length > 0) {
        await Promise.all(
          ids.map((id: any) =>
            Promise.all([fetchRatingsForHotel(id), fetchRevenueForHotel(id)])
          )
        );
      }
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (!selectedHotelId) return;
    void fetchRevenueForHotel(selectedHotelId);
    if (!ratingsMap[selectedHotelId]) void fetchRatingsForHotel(selectedHotelId);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedHotelId]);

  const fetchNewBookingsCount = async () => {
    try {
      const payload = {
        data: { filter: "", userId: getUserId() },
        page: 0,
        pageSize: 100,
        order: [["createdAt", "DESC"]],
      };
      const res = await getAllMyBookings(payload);
      const total =
        res?.data?.data?.count ??
        res?.data?.data?.total ??
        (Array.isArray(res?.data?.data?.rows) ? res.data.data.rows.length : 0);
      setNewBookingsCount(total ?? 0);
    } catch (err) {
      console.error("Error fetching new bookings count:", err);
      setNewBookingsCount(0);
    }
  };

  const fetchRatingsForHotel = async (hotelId: string) => {
    if (!hotelId) return;
    try {
      const payload = {
        data: { filter: "", hotelId },
        page: 0,
        pageSize: 1000,
        order: [["createdAt", "DESC"]],
      };
      const res = await getAllRatings(payload);
      const rows = res?.data?.data?.rows || [];
      const count = rows.length;
      let avg = 0;
      if (count > 0) {
        const sum = rows.reduce((s: number, r: any) => {
          const val = Number(r.rating ?? r.score ?? 0);
          return s + (isNaN(val) ? 0 : val);
        }, 0);
        avg = +(sum / count).toFixed(2);
      }
      setRatingsMap((m) => ({ ...m, [hotelId]: { avg, count } }));
      setRatingsListMap((m) => ({ ...m, [hotelId]: rows }));
    } catch (err) {
      console.error(`Error fetching ratings for hotel ${hotelId}:`, err);
      setRatingsMap((m) => ({ ...m, [hotelId]: { avg: 0, count: 0 } }));
      setRatingsListMap((m) => ({ ...m, [hotelId]: [] }));
    }
  };

  const fetchRevenueForHotel = async (hotelId: string) => {
    if (!hotelId) return;
    try {
      const payload = {
        data: { filter: "", hotelId,status: { $ne: "cancelled" }  },
        page: 0,
        pageSize: 1000,
        order: [["createdAt", "ASC"]],
      };
      const res = await getAllHotelRevenue(payload);
      const rows = res?.data?.data?.rows ?? [];
      let total = 0;
      const chart: any[] = [];
      if (Array.isArray(rows) && rows.length > 0) {
        rows.forEach((r: any, idx: number) => {
          const paise = Number(r.netAmountPaise || 0);
          const revenue = paise / 100;
          total += revenue;
          const name = r.createdAt ? dayjs(r.createdAt).format("DD MMM") : `Rec ${idx + 1}`;
          chart.push({ name, revenue });
        });
      }
      setRevenueMap((m) => ({ ...m, [hotelId]: { total } }));
      if (chart.length) setChartData(chart);
    } catch (err) {
      console.error(`Error fetching revenue for hotel ${hotelId}:`, err);
    }
  };

  // simple formatter for INR
  const formatINR = (val: number) =>
    val.toLocaleString("en-IN", { maximumFractionDigits: 0 });

  // NAV handlers: always navigate to dedicated pages
  const goToBookings = () => {
    if (!selectedHotelId) return;
    navigate(`/booking`);
  };
  const goToReviews = () => {
    const id = ratingHotelId || selectedHotelId;
    if (!id) return;
    navigate(`/review`);
  };
  const goToPayments = () => {
    if (!selectedHotelId) return;
    navigate(`/hotel-payment`);
  };

  // handlers for select changes
  const handleHotelChange = (event: any) => setSelectedHotelId(event.target.value);
  const handleRatingHotelChange = (event: any) => {
    const id = event.target.value;
    setRatingHotelId(id);
    void fetchRatingsForHotel(id);
  };

  // clickable metric card props
  const metrics = [
    {
      key: "newBookings",
      title: "New Bookings",
      value: newBookingsCount,
      icon: <CalendarMonth />,
      change: "8.70%",
      onClick: goToBookings,
      hoverText: "Go to Bookings",
    },
    {
      key: "guestRatings",
      title: "Guest ratings",
      value: ratingsMap[ratingHotelId] ? `${ratingsMap[ratingHotelId].avg}/5` : "—/5",
      icon: <StarRateRounded />,
      change: "-3.56%",
      onClick: goToReviews,
      hoverText: "Go to Reviews",
    },
    {
      key: "revenue",
      title: "Total Revenue",
      value: revenueMap[selectedHotelId] ? `₹${formatINR(revenueMap[selectedHotelId].total)}` : "₹0",
      icon: <CurrencyRupeeRounded />,
      change: "5.70%",
      onClick: goToPayments,
      hoverText: "Go to Payments",
    },
  ];

  // optimistic check-in/out handlers for passing to BookingTable
  const handleCheckIn = async (bookingId: string) => {
    if (!selectedHotelId) return;
    const prev = hotelBookings[selectedHotelId] ?? [];
    setHotelBookings((pb) => {
      const next = { ...pb };
      next[selectedHotelId] = (next[selectedHotelId] || []).map((b: any) =>
        b.id === bookingId ? { ...b, status: "checkedin" } : b
      );
      return next;
    });
    try {
      // call API if desired
    } catch (err) {
      setHotelBookings((pb) => ({ ...pb, [selectedHotelId]: prev }));
    }
  };

  const handleCheckOut = async (bookingId: string) => {
    if (!selectedHotelId) return;
    const prev = hotelBookings[selectedHotelId] ?? [];
    setHotelBookings((pb) => {
      const next = { ...pb };
      next[selectedHotelId] = (next[selectedHotelId] || []).map((b: any) =>
        b.id === bookingId ? { ...b, status: "checkedout" } : b
      );
      return next;
    });
    try {
      // call API if desired
    } catch (err) {
      setHotelBookings((pb) => ({ ...pb, [selectedHotelId]: prev }));
    }
  };

  return (
    <Box p={3} sx={{ background: color.thirdColor }}>
      <Grid container spacing={3}>
        {/* Header + Hotel select */}
        <Grid item xs={12}>
          <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
            <Typography variant="h5" sx={{ color: color.firstColor }}>Hotel Dashboard</Typography>
            <Box display="flex" gap={2} alignItems="center">
              <FormControl sx={{ minWidth: 300 }}>
                <Select value={selectedHotelId ?? ""} onChange={handleHotelChange} displayEmpty>
                  {hotels.map((h) => <MenuItem key={h.id} value={h.id}>{h.name}</MenuItem>)}
                </Select>
              </FormControl>
              <Button variant="outlined" onClick={() => { if (selectedHotelId) { void fetchRevenueForHotel(selectedHotelId); void fetchRatingsForHotel(selectedHotelId); } }}>Refresh</Button>
            </Box>
          </Box>
        </Grid>

        {/* Metrics */}
        <Grid item xs={12}>
          <Grid container spacing={2.5}>
            {metrics.map((item) => (
              <Grid key={item.key} item xs={12} sm={6} md={4}>
                <Card
                  onClick={item.onClick}
                  sx={{
                    background: color.thirdColor,
                    boxShadow: "0px 0px 14px rgba(0, 0, 0, 0.14)",
                    borderRadius: "12px",
                    minHeight: CARD_HEIGHT,
                    display: "flex",
                    alignItems: "center",
                    cursor: "pointer",
                    transition: "transform 0.15s ease",
                    "&:hover": { transform: "translateY(-4px)" },
                  }}
                >
                  <CardContent sx={{ width: "100%" }}>
                    <Box display="flex" justifyContent="space-between" alignItems="center">
                      <Typography color="#7c7c7c" fontSize={"14px"}>{item.title}</Typography>
                      <IconButton sx={{ background: color.firstColor, color: "white", borderRadius: "8px" }} size="small">{item.key === "revenue" ? <CurrencyRupeeRounded /> : item.key === "guestRatings" ? <StarRateRounded /> : <CalendarMonth />}</IconButton>
                    </Box>

                    <Box display="flex" gap={2} alignItems="center" mt={1}>
                      {item.key === "guestRatings" ? (
                        <FormControl size="small" sx={{ minWidth: 160 }}>
                          <Select size="small" value={ratingHotelId} onChange={handleRatingHotelChange} onClick={(e) => e.stopPropagation()}>
                            {hotels.map(h => <MenuItem key={h.id} value={h.id}>{h.name}</MenuItem>)}
                          </Select>
                        </FormControl>
                      ) : null}

                      <Typography variant="h5" fontWeight={600}>{item.value}</Typography>
                    </Box>

                    <Typography mt={2} display="flex" alignItems="center" gap={1} sx={{ color: item.change.includes("-") ? "red" : "green", width: "fit-content", px: 2, borderRadius: "4px", border: "solid 1px", fontSize: "14px" }}>
                      {item.change.includes("-") ? <TrendingDown /> : <TrendingUp />} {item.change} from last week
                    </Typography>

                    <Typography mt={1} color="#7c7c7c" fontSize="12px"><em>{item.hoverText}</em></Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Grid>

        {/* Booking table */}
        <Grid item xs={12}>
          <Card sx={{ background: color.thirdColor, borderRadius: 2 }}>
            <CardContent>
              <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
                <Typography fontWeight="bold" variant="h6">Upcoming Bookings</Typography>
                <Typography color="textSecondary">{hotels.find(h => h.id === selectedHotelId)?.name ?? "—"}</Typography>
              </Box>
              {selectedHotelId && (
                <BookingTable
                  bookings={hotelBookings[selectedHotelId] || []}
                  hotelId={selectedHotelId}
                  onCheckIn={handleCheckIn}
                  onCheckOut={handleCheckOut}
                />
              )}
            </CardContent>
          </Card>
        </Grid>

        {/* Charts */}
        <Grid item xs={12} md={6}>
          <Card sx={{ background: color.firstColor, color: "white", borderRadius: 2, padding: 2, height: 300 }}>
            <CardContent sx={{ p: 0, height: "100%" }}>
              <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
                <Typography fontWeight="bold" variant="h6">Booking Overview</Typography>
                <FormControl sx={{ width: "fit-content", color: "white" }}>
                  <Select size="small" value="Last 6 Months">
                    <MenuItem value="Last 6 Months">Last 6 Months</MenuItem>
                  </Select>
                </FormControl>
              </Box>
              <Box sx={{ height: "220px" }}>
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart data={chartData}>
                    <CartesianGrid strokeDasharray="3 3" stroke="white" />
                    <XAxis dataKey="name" stroke="white" style={{ fontSize: 10 }} />
                    <YAxis stroke="white" style={{ fontSize: 10 }} />
                    <Tooltip contentStyle={{ backgroundColor: color.firstColor, border: "solid 1.5px white", borderRadius: 4, color: "white" }} />
                    <Line type="monotone" dataKey="revenue" stroke="white" strokeWidth={3} dot={{ r: 6 }} />
                  </LineChart>
                </ResponsiveContainer>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} md={6}>
          <Card sx={{ background: color.firstColor, color: "white", borderRadius: 2, padding: 2, height: 300 }}>
            <CardContent sx={{ p: 0, height: "100%" }}>
              <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
                <Typography fontWeight="bold" variant="h6">Revenue</Typography>
                <FormControl sx={{ width: "fit-content", color: "white" }}>
                  <Select size="small" value="Last 6 Months">
                    <MenuItem value="Last 6 Months">Last 6 Months</MenuItem>
                  </Select>
                </FormControl>
              </Box>
              <Box sx={{ height: "220px" }}>
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart data={chartData}>
                    <CartesianGrid strokeDasharray="3 3" stroke="white" />
                    <XAxis dataKey="name" stroke="white" style={{ fontSize: 10 }} />
                    <YAxis stroke="white" style={{ fontSize: 10 }} />
                    <Tooltip contentStyle={{ backgroundColor: color.firstColor, border: "solid 1.5px white", borderRadius: 4, color: "white" }} />
                    <Line type="monotone" dataKey="revenue" stroke="white" strokeWidth={3} dot={{ r: 6 }} />
                  </LineChart>
                </ResponsiveContainer>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        {/* Reviews section */}
        <Grid item xs={12}>
          <Card sx={{ borderRadius: 2 }}>
            <CardContent>
              <Box display="flex" justifyContent="space-between" alignItems="center" mb={1}>
                <Typography fontWeight="bold" variant="h6">Guest Reviews</Typography>
                <Box display="flex" alignItems="center" gap={2}>
                  <Typography color="textSecondary">{hotels.find(h => h.id === selectedHotelId)?.name ?? "—"}</Typography>
                  <Typography color="textSecondary">{ratingsMap[selectedHotelId] ? `${ratingsMap[selectedHotelId].count} reviews • ${ratingsMap[selectedHotelId].avg}/5` : "No reviews"}</Typography>
                </Box>
              </Box>

              <Divider sx={{ mb: 2 }} />

              {(!ratingsListMap[selectedHotelId] || ratingsListMap[selectedHotelId].length === 0) ? (
                <Typography color="textSecondary">No reviews for this hotel.</Typography>
              ) : (
                <Box display="flex" flexDirection="column" gap={2}>
                  {ratingsListMap[selectedHotelId].map((r: any) => (
                    <Box key={r.id} sx={{ p: 2, borderRadius: 1, background: color.thirdColor }}>
                      <Box display="flex" justifyContent="space-between" alignItems="center">
                        <Box display="flex" gap={2} alignItems="center">
                          <Avatar src={r.userAvatar || undefined} />
                          <Box>
                            <Typography fontWeight={700}>{r.userName ?? r.userId ?? "Guest"}</Typography>
                            <Typography fontSize={12} color="textSecondary">{r.comment ? (r.comment.length > 120 ? r.comment.substring(0, 120) + "..." : r.comment) : "No comment"}</Typography>
                          </Box>
                        </Box>
                        <Box textAlign="right">
                          <Typography fontWeight={700}>{Number(r.rating ?? 0).toFixed(1)} / 5</Typography>
                          <Typography fontSize={12} color="textSecondary">{r.createdAt ? dayjs(r.createdAt).format("DD MMM YYYY, hh:mm A") : "-"}</Typography>
                        </Box>
                      </Box>
                    </Box>
                  ))}
                </Box>
              )}
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Dashboard;
