import { useState, useEffect } from "react";
import {
  Avatar,
  Box,
  Button,
  Chip,
  FormControl,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import color from "../../components/color";
import { editHotel, getHotel, updateBookings } from "../../services/services";

interface Booking {
  id: string;
  avatar?: string;
  geustName?: string;
  phoneNumber?: string;
  geustDetails?: any;
  checkInDate?: string;
  checkInTime?: string;
  checkOutDate?: string;
  checkOutTime?: string;
  status?: string;
  statusColor?: string;
  // include any other fields your booking objects have
}

interface BookingTableProps {
  bookings?: Booking[];
  hotelId?: string;
  onCheckIn?: (bookingId: string) => void;
  onCheckOut?: (bookingId: string) => void;
}

const BookingTable = ({ bookings = [], hotelId, onCheckIn, onCheckOut }: BookingTableProps) => {
  const [availability, setAvailability] = useState<string | null>(null);
  const [loadingAvailability, setLoadingAvailability] = useState(false);

  // local copy of bookings for optimistic updates
  const [localBookings, setLocalBookings] = useState<Booking[]>(bookings);

  // track loading booking ids (so we can disable only the clicked booking's buttons)
  const [loadingIds, setLoadingIds] = useState<string[]>([]);

  useEffect(() => {
    setLocalBookings(bookings);
  }, [bookings]);

  // Fetch hotel availability on mount / hotel change
  useEffect(() => {
    let mounted = true;
    if (!hotelId) {
      setAvailability(null);
      return;
    }

    setLoadingAvailability(true);
    getHotel(hotelId)
      .then((res) => {
        if (!mounted) return;
        const avail = res?.data?.data?.roomAvailable ?? "Unavailable";
        setAvailability(avail);
      })
      .catch(() => {
        if (!mounted) return;
        setAvailability("Unavailable");
      })
      .finally(() => {
        if (!mounted) return;
        setLoadingAvailability(false);
      });

    return () => {
      mounted = false;
    };
  }, [hotelId]);

  const toggleAvailability = () => {
    if (!hotelId || availability == null) return;

    const prev = availability;
    const newStatus = availability === "Available" ? "Unavailable" : "Available";
    setAvailability(newStatus);

    editHotel(hotelId, { roomAvailable: newStatus })
      .then(() => {
        console.log("Availability updated to", newStatus);
      })
      .catch((err) => {
        console.error("Error updating availability:", err);
        // Revert on error
        setAvailability(prev);
      });
  };

  // Button logic helpers (adjust statuses if your API uses different strings)
  const canCheckIn = (booking: Booking) => {
    const s = (booking.status || "").toLowerCase();
    return s === "pending" || s === "booked";
  };

  const canCheckOut = (booking: Booking) => {
    const s = (booking.status || "").toLowerCase();
    // backend status value for checked in is "checkedIn" -> lowercased becomes "checkedin"
    return s === "checkedin";
  };

  // helper to toggle id in loadingIds
  const setLoadingForId = (id: string, loading: boolean) => {
    setLoadingIds((prev) => {
      if (loading) {
        if (prev.includes(id)) return prev;
        return [...prev, id];
      } else {
        return prev.filter((x) => x !== id);
      }
    });
  };

  // map status -> color for optimistic update
  const statusColorFor = (status: string) => {
    const s = status.toLowerCase();
    if (s === "checkedin") return "rgba(163,228,215,0.8)";
    if (s === "checkedout") return "rgba(245,183,177,0.9)";
    return "rgba(0,0,0,0.08)";
  };

  // Update booking status (optimistic update + backend call + rollback on failure)
  const updateBookingStatus = async (bookingId: string, newStatus: "checkedIn" | "checkedOut") => {
    // snapshot for rollback
    const prev = localBookings.map((b) => ({ ...b }));

    setLoadingForId(bookingId, true);

    // optimistic update (store camelCase status)
    setLocalBookings((list) =>
      list.map((b) =>
        b.id === bookingId ? { ...b, status: newStatus, statusColor: statusColorFor(newStatus) } : b
      )
    );

    try {
      // === CALL BACKEND ===
      // NOTE: I call updateBookings as (id, payload). If your service expects a different signature,
      // replace the following line with the correct call.
      await updateBookings(bookingId, { status: newStatus });

      // notify parent so it can refresh canonical server data if needed
      if (newStatus === "checkedIn") onCheckIn?.(bookingId);
      else if (newStatus === "checkedOut") onCheckOut?.(bookingId);
    } catch (err) {
      // rollback on error
      console.error("Failed to update booking status", err);
      setLocalBookings(prev);
    } finally {
      setLoadingForId(bookingId, false);
    }
  };

  return (
    <Box
      sx={{
        padding: 2,
        background: color.thirdColor,
        boxShadow: "0px 0px 14px rgba(0, 0, 0, 0.14)",
        borderRadius: "12px",
      }}
    >
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
        <div>
          <Typography variant="h6" fontWeight="bold">
            New bookings
          </Typography>
          <Typography variant="body2" color="gray">
            {localBookings.length > 0 ? "Recent guest reservations list" : "No bookings found"}
          </Typography>
        </div>

        <FormControl sx={{ width: "fit-content", color: color.firstColor }}>
          <Button
            variant="contained"
            color={availability === "Available" ? "success" : "error"}
            onClick={toggleAvailability}
            disabled={!hotelId || loadingAvailability}
            sx={{ textTransform: "none" }}
          >
            {availability ?? (loadingAvailability ? "Loading..." : "Unavailable")}
          </Button>
        </FormControl>
      </Box>

      {localBookings.length > 0 ? (
        // make only the table area scrollable by setting maxHeight on TableContainer + sticky header
        <TableContainer component={Paper} sx={{ marginTop: 2, boxShadow: "none", maxHeight: 420 }}>
          <Table aria-label="bookings table" sx={{ background: color.thirdColor }} stickyHeader>
            <TableHead>
              <TableRow>
                {[
                  "Booking ID",
                  "Guest name",
                  "Phone Number",
                  "Checked In Date",
                  "Checked In Time",
                  "Checked Out Date",
                  "Checked Out Time",
                  "Status",
                  "Actions",
                ].map((header) => (
                  <TableCell
                    key={header}
                    sx={{ fontWeight: "bold", color: "gray", fontSize: "14px", whiteSpace: "nowrap" }}
                  >
                    {header}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>

            <TableBody>
              {localBookings.map((booking) => {
                const isLoading = loadingIds.includes(booking.id);
                return (
                  <TableRow key={booking.id} sx={{ border: "none" }}>
                    <TableCell sx={{ verticalAlign: "middle" }}>{booking.id}</TableCell>

                    <TableCell>
                      <Box display="flex" alignItems="center" gap={1}>
                        <Avatar src={booking.avatar} alt={booking.geustName ?? "guest"} />
                        <Typography>{booking.geustName ?? "Guest"}</Typography>
                      </Box>
                    </TableCell>
                    <TableCell>+{booking?.geustDetails?.phoneNumber ?? "-"}</TableCell>

                    <TableCell>{booking.checkInDate ?? "-"}</TableCell>
                    <TableCell>{booking.checkInTime ?? "-"}</TableCell>
                    <TableCell>{booking.checkOutDate ?? "-"}</TableCell>
                    <TableCell>{booking.checkOutTime ?? "-"}</TableCell>

                    <TableCell>
                      <Chip
                        label={booking.status ?? "unknown"}
                        sx={{
                          backgroundColor: booking.statusColor ?? statusColorFor(booking.status ?? ""),
                          color: "#000",
                          fontWeight: "bold",
                          textTransform: "capitalize",
                        }}
                      />
                    </TableCell>

                    {/* Actions cell */}
                    <TableCell>
                      <Box display="flex" gap={1} alignItems="center">
                        <Button
                          size="small"
                          variant="contained"
                          sx={{
                            background: color.firstColor,
                            textTransform: "none",
                            borderRadius: "20px",
                            "&:disabled": { opacity: 0.6 },
                          }}
                          disabled={!canCheckIn(booking) || isLoading}
                          onClick={() => updateBookingStatus(booking.id, "checkedIn")}
                        >
                          {isLoading && canCheckIn(booking) ? "Processing..." : "Check-In"}
                        </Button>

                        <Button
                          size="small"
                          variant="outlined"
                          sx={{
                            color: color.firstColor,
                            borderColor: color.firstColor,
                            textTransform: "none",
                            borderRadius: "20px",
                            "&:disabled": { opacity: 0.5 },
                          }}
                          disabled={!canCheckOut(booking) || isLoading}
                          onClick={() => updateBookingStatus(booking.id, "checkedOut")}
                        >
                          {isLoading && canCheckOut(booking) ? "Processing..." : "Check-Out"}
                        </Button>
                      </Box>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      ) : (
        <Box display="flex" justifyContent="center" alignItems="center" minHeight="200px" sx={{ background: color.thirdColor }}>
          <Typography variant="body1" color="textSecondary">
            No bookings available for this hotel
          </Typography>
        </Box>
      )}
    </Box>
  );
};

export default BookingTable;
